<template>
    <div class="flex-page" ref="flexPage">
        <van-nav-bar class="flex-full">
            <div slot="left" class="leftBar">
                <div class="backIcon" @click="goToBack">
                    <img src="@/assets/images/common/nav_return_black.png" alt="">
                </div>
                <div class="searchInput" @click="goToGoodsSearch()">搜索好物</div>
                <img class="voiceIcon" src="@/assets/images/mall/mall_icon_voice.png" alt="" @click="goToSpeech">
                <dialog-bar v-model="sendVal" type="danger" v-bind:title="recognizeResult" content="您可以说：“花生油”<br />“我想要买花生油”<br />..." v-on:cancel="clickCancel()" @danger="clickDanger()" @confirm="clickConfirm()" dangerText="Delete" ></dialog-bar>
            </div>
            <div slot="right" class="rightBar" @click="goToShoppingCart()">
                <img src="@/assets/images/mall/mall_icon_shoppingcart_black.png" alt="">
                <div class="goodsNum" v-show="skuTypeNum!=0">
<!--                    <div>{{skuTypeNum}}</div>-->
                    <van-icon class="cart" :info="skuTypeNum!=0?(skuTypeNum<=99?skuTypeNum+'':'99+'):''"></van-icon>
                </div>
            </div>
        </van-nav-bar>
        <div class="post-wrapper" ref="contentScroll" :style="{height:clientHeight-46+'px'}">
            <div class="post-content">
                <div class="tabsNav">
                    <div class="tabsContent" ref="tabsContent">
                        <div class="tabsList">
                            <div class="tabsItem" v-for="(classifyItem,classifyIndex) in classifyList"
                                 :key="classifyIndex" @click="changeMoreList(classifyIndex)">
                                <div class="tabsTxt"
                                     :class="tabActive==classifyIndex?'tabTitleItemActive':'tabTitleItem'">
                                    {{classifyItem.cateName}}
                                </div>
                                <div class="lineBox" v-if="classifyIndex!=classifyList.length-1"></div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="sortBtnList">
                    <div class="volumeBox" @click="sortMethod=3">
                        <div class="volumeBtn">销量</div>
                        <div class="redLineBox" v-show="sortMethod==3"><img src="@/assets/images/mall/redLine.png" alt=""></div>
                    </div>
                    <div @click="priceSort()">
                        <div class="priceBtn">
                            <div class="priceTxt">价格</div><img :src="sortMethod==''||sortMethod==3?icon.filter:(sortMethod==1?icon.filterUp:icon.filterDown)" alt="">
                        </div>
                        <div class="redLineBox" v-show="sortMethod==1||sortMethod==2"><img src="@/assets/images/mall/redLine.png" alt=""></div>
                    </div>
                </div>
                <van-list
                        class="goodsVantList"
                        v-model="loading"
                        :finished="finished"
                        @load="onLoad"
                        style="width:100%;"
                        :offset="offsetHeight"
                >
                    <div class="goodsList">
                        <div class="goodsItem" v-for="(goodsItem,goodsIndex) in goodsListData"
                             :key="'goodsIndex'+goodsIndex" @click="goToProduct(goodsItem)">
                            <div class="goodsImg">
                                <img v-lazy="goodsItem.spuPicUrl" alt="" :key="goodsItem.spuPicUrl">
                                <div class="outSellBox" v-show="goodsItem.storeNumber==0">补货中</div>
                                <div class="onlyRemainTxt" v-show="goodsItem.storeNumber<=goodsItem.threshold&&goodsItem.storeNumber!=0">仅剩{{goodsItem.storeNumber}}件</div>
                            </div>
                            <div class="goodsInfo">
                                <div class="goodsName">{{goodsItem.spuName}}</div>
                                <div class="labelList">
                                    <div class="labelItem">{{goodsItem.shopName}}</div>
                                    <div class="labelItem" v-show="goodsItem.couponFlag==1">券</div>
                                </div>
                                <div class="goodsBottomInfo">
                                    <div class="goodsPrice">
                                        <div class="priceAndSale">
                                            <div class="goodsPriceNum">￥{{goodsItem.skuPrice}}</div>
                                            <div class="saleNum">月售{{goodsItem.showSale | threshold}}</div>
                                        </div>
                                        <div class="lineGoodsPriceNum" v-if="goodsItem.priceLine">￥{{goodsItem.priceLine}}</div>
                                    </div>
                                    <div class="addBtn" @click.stop="addGoods(goodsItem)">
                                        <img v-show="goodsItem.storeNumber!=0" src="@/assets/images/mall/mall_icon_add.png" alt="">
                                        <img v-show="goodsItem.storeNumber==0" src="@/assets/images/mall/mall_icon_add_gray.png" alt="">
                                    </div>
                                </div>

                            </div>
                        </div>

                    </div>
                    <div class="nullDataBox" v-show="goodsListData.length==0">
                        <img class="nullDataImg" src="@/assets/images/default/no_shop_con_img.png" alt="">
                        <div class="nullDataTxt">该分类无商品</div>
                    </div>
                    <div class="finishedBox" :style="{paddingBottom:this.$stbHeight+'px'}" v-show="finished&&goodsListData.length!=0">
                        <img src="@/assets/images/mall/mall_logo.png" alt="">
                    </div>
                </van-list>
            </div>
        </div>

    </div>
</template>

<script>
    import {
        getGoodsForCatePageService,
        addGoodsService,
        getSkuTypeNumService
    } from "../../services/goodsService";
    import {mapActions, mapState} from "vuex";
    import {Toast} from "vant";
    import SpeechRecognizerDialog from "../../common/currency/SpeechRecognizerDialog";

    export default {
        name: "moreGoods",
        data() {
            return {
                clientHeight:0,
                skuTypeNum:0, //购物车种类数量
                goodsCateItem:{}, // 一级分类数据列表
                classifyList: [], // 分类列表
                goodsListData: [],//商品集合
                setVantHeight: {},  //设置为你推荐的高度

                loading: false, //停止加载
                finished: false,//没有更多
                offsetHeight:600,
                isLastPage: false,//是否有更多数据
                sortMethod:"", //  1 从小到大 2从大到小 3 销量
                isPriceSort:false, // false 从小到大 true 从大到小

                tabsScrollLeft:"",
                contentScrollTop:"",
                tabActive: 0,
                icon:{
                    filter:require("@/assets/images/mall/icon_filter.png"),
                    filterDown:require("@/assets/images/mall/icon_filter_down.png"),
                    filterUp:require("@/assets/images/mall/icon_filter_up.png"),
                },
                sendVal:false,
                recognizeResult:"正在聆听",
                isfrist:true,
            }
        },
        components: {
            'dialog-bar' : SpeechRecognizerDialog
        },
        watch: {
            sortMethod:function (val) {
                if (val=="") {
                    console.log("-------------")
                    return;
                }
                this.$store.commit("moreGoodsPageNumberChange", {
                    custPageNum: true,
                    pageNumber: 1
                });
                this.getMoreGoods(this.pageNumber);
            },
        },
        computed: mapState({
            pageNumber: state => state.productPage.moreGoodsPageNumber,
        }),
        filters:{
            threshold(data){
                return data>=10000?((data+1000) / 10000).toFixed(1)+'万+':data
            }
        },
        created() {
            this.$nextTick(()=>{
                this.clientHeight = this.$refs.flexPage.clientHeight
            })
            this.goodsCateItem = this.$route.params.goodsCateItem;
        },
        mounted() {
            // this.getGoodsForCatePage(1); //获取一级分类列表，和数据
            // this.getSkuTypeNum(); //获取购物车种类数量
        },
        activated(){
            if(this.$route.params.goodsCateItem){
                this.goodsCateItem = this.$route.params.goodsCateItem;
                this.getGoodsForCatePage(1);//获取一级分类列表，和数据
            }else{
                this.$nextTick(()=>{
                    this.$refs.tabsContent.scrollLeft = this.tabsScrollLeft;
                    this.$refs.contentScroll.scrollTop = this.contentScrollTop;
                })
            }
            this.getSkuTypeNum(); //获取购物车种类数量
        },
        methods: {
            ...mapActions(["getGoodsForCate","getGoodsForCatePageStore"]),
            //语音搜索
            goToSpeech(){
                var _this = this;
                var ifly = cordova.plugins.IflyForXwjPlugin;
                _this.recognizeResult = "正在聆听";
                const platform = navigator.userAgent;
                const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
                if (isiOS) {
                    _this.sendVal=true;
                    ifly.startListen(function (result) {
                            if (result){
                                var resultcatch = result.replace(/[\ |\~|\`|\!|\@|\#|\$|\%|\^|\&|\*|\(|\)|\-|\_|\+|\=|\||\\|\[|\]|\{|\}|\;|\:|\"|\'|\,|\<|\。|\>|\/|\？]/g,"");
                                _this.recognizeResult = resultcatch;
                                setTimeout(function () {
                                    _this.sendVal = false;
                                    console.log(resultcatch);
                                    _this.goToGoodsSearch(resultcatch);
                                },"2000");
                            }else {
                                _this.sendVal = false;
                                console.log('没有值')
                                Toast('我没听清您说什么')
                            }
                        },
                        function (message) {
                            _this.sendVal = false;
                            console.log("err" + message);
                        },false,true
                    );
                }else {
                    var list = [
                        'android.permission.RECORD_AUDIO'
                    ];
                    var permissions = cordova.plugins.permissions;
                    permissions.requestPermissions(list,function (status) {
                        if(status.hasPermission){
                            _this.sendVal=true;
                            ifly.startListen(function (result) {
                                    if (result){
                                        console.log(result);
                                        var resultcatch = result.replace(/[\ |\~|\`|\!|\@|\#|\$|\%|\^|\&|\*|\(|\)|\-|\_|\+|\=|\||\\|\[|\]|\{|\}|\;|\:|\"|\'|\,|\<|\。|\>|\/|\？]/g,"");
                                        _this.recognizeResult = resultcatch;
                                        setTimeout(function () {
                                            _this.sendVal = false;
                                            _this.goToGoodsSearch(resultcatch);
                                        },"2000");
                                    }else {
                                        _this.sendVal = false;
                                        console.log('没有值')
                                    }
                                },
                                function (message) {
                                    _this.sendVal = false;
                                    console.log("err" + message);
                                },false,true
                            );
                        }
                    },function (status) {
                        alert("请打开麦克风权限");
                    });
                }
            },
            //点击一级分类标签
            changeMoreList(index) {
                console.log(index)
                this.tabActive = index;
                this.goodsCateItem = this.classifyList[this.tabActive];
                this.sortMethod = "";
                this.$refs.tabsContent.scrollLeft = this.tabActive*100-(this.$refs.tabsContent.offsetWidth/2)+75;
                this.$store.commit("moreGoodsPageNumberChange", {
                    custPageNum: true,
                    pageNumber: 1
                });
                this.getGoodsForCatePage(this.pageNumber);
            },
            //加载更多商品
            onLoad() {
                if(this.sortMethod == ""){
                    if(this.isfrist){
                        this.isfrist = false;
                    }else{
                        this.getGoodsForCatePage(this.pageNumber, false);
                    }
                }else{
                    this.getMoreGoods(this.pageNumber, false);
                }
            },
            //价格排序
            priceSort(){
                if (!this.isPriceSort){
                    this.sortMethod = 1;
                }else{
                    this.sortMethod = 2;
                }
                this.isPriceSort = !this.isPriceSort;
            },
            /**
             * 获取一级分类列表，和数据
             * @param
             * areaCode  Number      小区CODE
             * page   Number     页数
             * rows  Number  个数
             **/
            getGoodsForCatePage(pageNumber,isRefresh = true) {
                let paramData = {
                    areaCode: this.$userInfo.AREA_CODE,
                    cateCode: this.goodsCateItem.cateCode,
                    page: pageNumber,
                    rows: 10,
                }
                this.getGoodsForCatePageStore(paramData).then(res => {
                    if (res.resultCode == '0') {
                        if(res.data.goodsList.list.length!=0){
                            res.data.goodsList.list.map((item,index)=>{
                                if(index%2==0){
                                    item.spuPicUrl = item.spuPicUrl.replace(/xwjfile.juhaolian.cn/,'xwjfilea.juhaolian.cn')
                                }
                            })
                        }
                        if(isRefresh){
                            this.classifyList = res.data.cateList;
                            this.goodsListData = res.data.goodsList.list;
                            this.$store.commit("moreGoodsPageNumberChange", {
                                custPageNum: true,
                                pageNumber: 2
                            });
                            this.classifyList.map((classItem,classIndex)=>{
                                if(classItem.cateCode == this.goodsCateItem.cateCode){
                                    this.tabActive = classIndex
                                }
                            })
                            this.$nextTick(()=>{
                                this.$refs.tabsContent.scrollLeft = this.tabActive*100-(this.$refs.tabsContent.offsetWidth/2)+75;
                            })
                        }else{
                            this.goodsListData = this.goodsListData.concat(res.data.goodsList.list);
                        }



                        this.loading = false;
                        this.isfrist = false;
                        this.finished = !res.data.goodsList.hasNextPage;
                    }

                }).catch(error => {
                    this.loading = false;
                    this.finished = false;
                });
            },
            /**
             * 拉取选中分类的更多数据
             * @param
             * areaCode  Number      小区CODE
             * cateCode Number  分类code
             * page   Number     页数
             * rows  Number  个数
             **/
            getMoreGoods(pageNumber, isRefresh = true) {
                let paramData = {
                    areaCode: this.$userInfo.AREA_CODE,
                    cateCode: this.classifyList[this.tabActive].cateCode,
                    page: pageNumber,
                    rows: 10,
                    sortMethod:this.sortMethod
                }
                this.getGoodsForCate(paramData).then(res => {
                    if(res.data.list.length!=0){
                        res.data.list.map((item,index)=>{
                            if(index%2==0){
                                item.spuPicUrl = item.spuPicUrl.replace(/xwjfile.juhaolian.cn/,'xwjfilea.juhaolian.cn')
                            }
                        })
                    }
                    if (!isRefresh) {
                        this.goodsListData = this.goodsListData.concat(res.data.list);
                    } else {
                        this.goodsListData = res.data.list;
                    }
                    this.loading = false;
                    this.finished = !res.data.hasNextPage;
                }).catch(error => {
                });
            },
            /**
             * 添加购物车
             * @param
             * ownerCode Number  业主CODE
             * spuCode   Number  商品CODE
             * skuNo     Number  商品分类CODE
             * shopCode  Number  店铺CODE
             * skuCount  Number  商品数量
             * mark      Number  业主App标识
             */
            addGoods(spuItem){
                if(spuItem.storeNumber==0){
                    Toast('该商品正在努力补货中哦')
                    return;
                }
                let paramData = {
                    ownerCode:this.$userInfo.OWNER_CODE,
                    spuCode:spuItem.spuCode,
                    skuNo:spuItem.skuNo,
                    shopCode:spuItem.shopCode,
                    skuCount:1,
                    mark:1,
                }
                addGoodsService(paramData).then(res=>{
                    if (res.resultCode == 0) {
                        this.getSkuTypeNum(true);
                    }else if (res.resultCode == '20100' && res.data.result == '0') {
                    this.$toast('您所添加商品超过最大购买量了哦');
                }
                }).catch(error=>{
                })
            },
            /**
             * 查询购物车数量
             * ownerCode  Number  业主CODE
             */
            getSkuTypeNum(isAddGoods=false){
                let paramData = {
                    ownerCode:this.$userInfo.OWNER_CODE
                }
                getSkuTypeNumService(paramData).then(res=>{
                    if (res.resultCode == 0) {
                        if (isAddGoods){
                            Toast({
                                message: '添加购物车成功',
                                duration:1000
                            });
                        }
                        this.skuTypeNum = res.data.skuTypeNum;
                    }
                }).catch(error=>{
                })
            },
            //返回上一页
            goToBack(){
                this.isfrist = true;
                this.$router.go(-1);
            },
            //去搜索页面
            goToGoodsSearch(speechTxt){
                this.tabsScrollLeft = this.$refs.tabsContent.scrollLeft;
                this.contentScrollTop = this.$refs.contentScroll.scrollTop;
                    this.$router.push({
                    name: 'goodsSearch',
                    params: {
                        speechTxt : speechTxt,
                        isRefresh : 1,
                    }
                })
            },
            //去购物车页面
            goToShoppingCart(){
                this.tabsScrollLeft = this.$refs.tabsContent.scrollLeft;
                this.contentScrollTop = this.$refs.contentScroll.scrollTop;
                this.$router.push({
                    name: 'shoppingCart',
                    params: {

                    }
                })
            },
            //去商品详情页
            goToProduct(goodsItem){
                this.tabsScrollLeft = this.$refs.tabsContent.scrollLeft;
                this.contentScrollTop = this.$refs.contentScroll.scrollTop;
                this.$router.push({
                    name: 'goodsDetail',
                    params: {
                        spuCode:goodsItem.spuCode
                    }
                })
            },
        },

    }
</script>

<style scoped lang="scss">
    /deep/.loading{
        display: none;
    }
    /*/deep/.img-wraper{*/
    /*    display: none !important;*/
    /*}*/
    .van-tab {
        padding: 0 !important;
    }
    .flex-page{
        width: 100%;
        height: 100%;
        position: absolute;
        .post-wrapper{
            height: 100%;
            overflow: scroll;
        }
    }

    /*导航栏*/
    .leftBar {
        height: 46px;
        position: relative;

        .backIcon {
            position: absolute;
            top: 0;
            left: -3px;

            img {
                margin-top: 8px;
                width: 30px;
            }
        }

        .voiceIcon {
            width: 20px;
            height: 20px;
            position: absolute;
            top: 12px;
            right: 8px;
        }
    }

    .searchInput {
        width: 275px;
        height: 34px;
        background: #F7F7F7;
        border-radius: 2px;
        border: 0;
        padding-left: 32px;
        padding-right: 32px;
        font-family: PingFangSC-Regular;
        font-size: 13px;
        color: #9A9A9A;
        text-align: left;
        letter-spacing: -0.04px;
        line-height: 34px;
        background-image: url(../../assets/images/mall/search_black.png);
        background-repeat: no-repeat;
        -webkit-background-size: 14px 14px;
        background-size: 14px 14px;
        background-position: 10px 9px;
        position: relative;
        top: 6px;
        box-sizing: border-box;
        margin-left: 37px;
    }

    .rightBar {
        width: 30px;
        height: 46px;
        position: relative;

        img {
            width: 30px;
            height: 30px;
            position: absolute;
            top: 8px;
            right: 0px;
        }

        .goodsNum {
            width: 14px;
            height: 14px;
            text-align: center;
            line-height: 14px;
            border-radius: 50%;
            letter-spacing: -0.02px;
            /*background: #E31419;*/
            position: absolute;
            top: 6px;
            right: -4px;
            font-size: 9px;
            color: #FFFFFF;
        }
    }

    .tabTitleList {
        display: flex;
        align-items: center;
        /*margin-top: 14px;*/
    }

    .tabTitleItem {
        font-family: PingFangSC-Medium;
        font-size: 16px;
        color: #333333;
        letter-spacing: 1px;
        /*border-right: 1px solid #979797;*/
    }


    .tabsNav {
        background: #FFFFFF;
        box-shadow: 0 0 1px 0 rgba(100,100,100,0.50);
        .tabsContent {
            height: 44px;
            overflow-y: scroll;
            align-items: center;
            padding-left: 15px;
            padding-right: 15px;

            .tabsList {
                width: 1000px;
                display: flex;
                align-items: center;
            }

            .tabsItem {
                display: flex;
                align-items: center;
                width: 102px;
                .tabsTxt {
                    width: 100px;
                    line-height: 44px;
                    /*padding: 0 15px;*/
                    font-family: PingFangSC-Medium;
                    font-size: 16px;
                    color: #333333;
                    letter-spacing: 1px;
                    text-align: center;
                    white-space:nowrap;
                    overflow: hidden;
                }

                .tabTitleItemActive {
                    font-family: PingFangSC-Medium;
                    font-size: 16px;
                    color: #17B7BD;
                    letter-spacing: 1px;
                }

            }
        }
        .tabsContent::-webkit-scrollbar {
            display: none;
        }
    }

    .lineBox {
        width: 0.029rem;
        height: 12px;
        background-color: #979797;
    }

    /*排序按钮列表*/
    .sortBtnList {
        /*width: 100%;*/
        display: flex;
        /*align-items: center;*/
        justify-content: flex-end;
        padding-right: 15px;
        margin-top: 15px;

        .volumeBox {
            margin-right: 25px;
        }

        .volumeBtn {
            font-family: PingFangSC-Medium;
            font-size: 14px;
            color: #4A4A4A;
            letter-spacing: -0.04px;
            line-height: 18px;
        }

        .priceBtn {
            display: flex;
            align-items: center;
            .priceTxt {
                font-family: PingFangSC-Regular;
                font-size: 14px;
                color: #4A4A4A;
                letter-spacing: -0.04px;
                line-height: 18px;
            }
            img {
                width: 9px;
                margin-left: 2px;
            }
        }
        .redLineBox {
            width: 20px;
            height: 3px;
            margin: 0 auto;
            display: flex;
            align-items: center;

            img {
                width: 20px;
            }
        }

    }

    .goodsVantList {
        margin-top: 15px;
        overflow: hidden;
    }
    .goodsList{
        padding: 0 15px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .goodsItem {
        width: 168px;
        background: #FFFFFF;
        box-shadow: 0 0 5px 0 rgba(194, 194, 194, 0.50);
        border-radius: 4px;
        margin-bottom: 10px;
        .goodsImg {
            width: 100%;
            height: 168px;
            overflow: hidden;
            position: relative;
            border-radius: 4px 4px 0 0;
            img {
                width: 100%;
            }
            .outSellBox{
                width: 70px;
                height: 70px;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);
                border-radius: 50%;
                background: rgba(0, 0, 0, 0.50);
                font-size: 12px;
                color: #FFFFFF;
                letter-spacing: -0.04px;
                line-height: 70px;
                text-align: center;
            }
            .onlyRemainTxt{
                width: 100%;
                height: 25px;
                text-align: center;
                position: absolute;
                bottom: 0;
                background: rgba(0, 0, 0, 0.50);
                font-size: 12px;
                color: #FFFFFF;
                letter-spacing: -0.04px;
                line-height: 25px;
            }
        }

        .goodsInfo {
            padding: 3px 5px 5px;

            .goodsName {
                height: 40px;
                overflow: hidden;
                line-height: 20px;
                font-family: PingFangSC-Medium;
                font-size: 14px;
                color: #333333;
                letter-spacing: -0.05px;
            }

            .labelList {
                display: flex;
                flex-wrap: wrap;
                margin-top: 5px;

                .labelItem {
                    padding: 0 4px;
                    font-family: PingFangSC-Medium;
